.introContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: left; // 텍스트 정렬을 왼쪽으로
  padding: 20px;
  color: #000;
  background: #bfbdba;
  height: 100vh;
  margin: 100px;
  width: 1500px;
  margin: 0 auto;
}

.imageContainer {
  flex: 4;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  overflow: hidden;
  // border: 1px solid #000;
  margin-right: 100px; // 이미지와 텍스트 간격
}

.profileImage {
  width: 100%;
  // height: auto;
  object-fit: cover; // 이미지 비율 유지하며 잘림
}

.textContainer {
  border: 1px solid #000;
  flex: 6;
  display: flex;
  flex-direction: column; // 텍스트를 세로로 정렬
}

.title {
  font-size: 4rem;
  font-weight: 700;
  margin-bottom: 10px;
  border: 1px solid #000;
}

.highlight {
  color: #732f20; // 강조 색상
}

.description {
  font-size: 1.2rem;
  max-width: 800px;
  line-height: 1.5;
  margin-bottom: 20px;
}

.downloadButton {
  padding: 10px 20px;
  background-color: #732f20;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;

  &:hover {
    background-color: darken(#732f20, 10%);
  }
}
