.about {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 80vh;
  color: #000;
  overflow: hidden;
  padding: 0 20px;
  // background-color: #0f0e0e;
  position: relative;
}

.asteriskContainer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: -1;
  overflow: hidden;
}

.asterisk {
  font-size: 40rem;
  color: rgba(0, 0, 0, 0.05);
  animation: rotate 20s linear infinite;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.title {
  font-size: 3rem;
  font-weight: 800;
  text-align: center;
  margin-bottom: 20px;
  position: relative;
  z-index: 1;
  font-style: italic;
  color: #732f20;
}

.description {
  font-size: 1.2rem;
  text-align: center;
  max-width: 800px;
  line-height: 1.5;
  position: relative;
  z-index: 1;
  font-weight: 500;
}
